import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class CommonService {
  constructor(private http: HttpClient) { }

  adminLogin(data: any): boolean {
    if (data.username === 'root' && data.password === 'root1234') {
      return true;
    } else {
      return false;
    }
  }

  getCategoryNameList(): Observable<any> {
    return this.http.get<any>('https://5fff0b86a4a0dd001701b5ec.mockapi.io/categoryList')
      .pipe();
  }

  getItemList(): Observable<any> {
    return this.http.get<any>('https://5fff0b86a4a0dd001701b5ec.mockapi.io/itemList')
      .pipe();
  }

  getItemListByCategory(categoryId: any): Observable<any> {
    return this.http.get<any>('https://5fff0b86a4a0dd001701b5ec.mockapi.io/itemList?categoryId=' + categoryId)
      .pipe();
  }

  getItemById(itemId: any): Observable<any> {
    return this.http.get<any>('https://5fff0b86a4a0dd001701b5ec.mockapi.io/itemList?id=' + itemId)
      .pipe();
  }
}
