import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DashboardComponent } from './components/admin/dashboard/dashboard.component';
import { LoginComponent } from './components/admin/login/login.component';
import { CategoryListComponent } from './components/category-list/category-list.component';
import { HomeComponent } from './components/home/home.component';
import { ItemDetailsComponent } from './components/item-details/item-details.component';

const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'magic-moment', component: HomeComponent },
  { path: 'magic-moment/itemDetails', component: ItemDetailsComponent },
  { path: 'magic-moment/categoryList', component: CategoryListComponent },

  
  { path: 'magic-moment/adminLogin', component: LoginComponent },
  { path: 'magic-moment/adminDashboard', component: DashboardComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
