import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CommonService } from 'src/app/services/common.service';

@Component({
  selector: 'app-category-list',
  templateUrl: './category-list.component.html',
  styleUrls: ['./category-list.component.scss']
})
export class CategoryListComponent implements OnInit {
  itemList: any = [];
  hasData: boolean = false;
  title: string = '';
  constructor(
    private commonService: CommonService,
    private route: ActivatedRoute,
    private router: Router
  ) { }

  ngOnInit(): void {
    const categoryId = this.route.snapshot.paramMap.get('id');
    if (categoryId) {
      this.getTitle(categoryId);
      this.commonService.getItemListByCategory(categoryId).subscribe(
        (data) => {
          if (data && data.length > 0) {
            this.hasData = true;
            data.forEach((element: any) => {
              this.itemList.push({
                id: element.id,
                name: element.name,
                price: element.price,
                images: element.images,
                categoryId: element.categoryId
              });
            });
          } else {
            this.hasData = false;
          }
        }
      );
    }
  }

  getTitle(id: string) {
    switch (id) {
      case '1610550397974':
        this.title = 't-shirt';
        break;
      case '1610553176922':
        this.title = 'Bag';
        break;
      case '1610553189402':
        this.title = 'Key ring';
        break;
      case '1610553197596':
        this.title = 'Mug';
        break;
      case '1610553197596':
        this.title = 'Mobile Cover';
        break;
    }
  }

  goToHome() {
    this.router.navigate(['magic-moment']);
  }

  itemDetailsRedirect(id: any) {
    this.router.navigate(['magic-moment/itemDetails', { id: id }]);
  }

}
